.demo{
  color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
}

.Editor{
  &__Inner{
    overflow: hidden;
    display: flex;
  }
  &__Tops{
    display: flex;
  }
  &__Top{
    width: 100%;
    height: 4rem;
    line-height: 4rem;
    text-align: center;
    &--left{
      width: 50%;
      background-color: #282c34;
    }
    &--right{
      width: 50%;
      background-color: #21252b;
      box-sizing: border-box;
      border-left: 0.2rem solid #181a1f;
      border-bottom: 0.2rem solid #181a1f;
    }
  }
  &__File{
    color: #fff;
    font-size: 1.2rem;
    position: relative;
    &::before{
      content: "";
			position: absolute;
			left: 8rem;
			top: 1rem;
			width: 1.7rem;
			height: 1.7rem;
			background-repeat: no-repeat;
			background-size: cover;
    }
    &--pug{
      &::before{
				background-image: url("../img/pug_02.png");
			}
    }
    &--sass{
      &::before{
        top: 1.3rem;
        background-image: url("../img/sass.png");
      }
    }
  }
  &__Left{
    width: 40%;
    height: 100vh;
    overflow-y: scroll;
    margin-left: 20%;
    background-color: #282c34;
    box-sizing: border-box;
    border-left: 0.1rem solid #181a1f;
    border-right: 0.1rem solid #181a1f;
  }
  &__Right{
    width: 40%;
    height: 100vh;
    overflow-y: scroll;
    background-color: #282c34;
    box-sizing: border-box;
    border-left: 1px solid #181a1f;
    border-left: .1rem solid #181a1f;
    border-right: 1px solid #181a1f;
    border-right: .1rem solid #181a1f;
  }
  &__InputArea{
    height: 100%;
    overflow: scroll;
    display: flex;
  }
  &__CodeNumber{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding-left: 3.5%;
    span{
      color: #4b5364;
      display: block;
      margin-bottom: 0.3rem;
      font-size: 1rem;
    }
  }
  &__CodeBox{
    width: 100%;
    padding: 0 5%;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
  }
  &__Codes{
    display: flex;
    flex-direction: column;
  }
  &__Code{
    font-size: 1rem;
    letter-spacing: 0.1rem;
    word-wrap: break-word;
    &--grey{
      color: #bbb;
    }
    &--blue{
      color: #77acc5;
    }
    &--purple{
      color: #7483d2;
    }
  }
}
