.l-Navigation {
	&__Inner {
		width: 20%;
		height: 100vh;
		background-color: #21252b;
		position: fixed;
		top: 0;
		left: 0;
	}

	&__Title {
		font-size: 1.4rem;
		text-align: center;
		color: #fff;
		padding: 1.5rem 0;
	}

	&__Items {
		color: #9da5b4;
	}

	&__Item {
		position: relative;
		font-size: 1.4rem;
		&::after,
		&::before {
			content: "";
		}
		&::before {
			position: absolute;
			left: -4.4rem;
			width: 0.4rem;
			height: 0.4rem;
			border-bottom: 0.15rem solid #9da5b4;
			border-right: 0.15rem solid #9da5b4;
		}
		&::after {
			position: absolute;
			left: -2.6rem;
			top: -0.2rem;
			background-image: url("../img/folder.svg");
      background-repeat: no-repeat;
      width: 1.7rem;
      height: 1.7rem;
      color: #9da5b4;
		}
		&--donw {
			&::before {
				top: -0.3rem;
				transform: rotate(45deg);
			}
		}
		&--right {
			&::before {
				top: 0.1rem;
				transform: rotate(-45deg);
			}
		}
	}

	&__File{
		position: relative;
		font-size: 1.4rem;
		margin-left: 1.6rem;
		&::before{
			content: "";
			position: absolute;
			left: -2.3rem;
			top: -0.15rem;
			width: 1.7rem;
			height: 1.7rem;
			background-repeat: no-repeat;
			background-size: cover;
		}
		&--html{
			&::before{
				background-image: url("../img/html.png");
			}
		}
		&--node{
			&::before{
				background-image: url("../img/node.png");
			}
		}
		&--babele{
			&::before{
				background-image: url("../img/babel.png");
			}
		}
		&--editor{
			&::before{
				background-image: url("../img/editor.png");
			}
		}
		&--gulp{
			&::before{
				background-image: url("../img/gulp.png");
			}
		}
		&--json{
			&::before{
				background-image: url("../img/json.png");
			}
		}
		&--yarn{
			&::before{
				background-image: url("../img/yarn.png");
			}
		}
		&--pug{
			&::before{
				background-image: url("../img/pug.png");
			}
			&::after{
				content: "";
		    top: -5px;
		    background-color: rgba(255,255,225,.1);
		    width: 213%;
		    height: 25px;
		    left: -113%;
		    position: absolute;
		    z-index: 0;
			}
		}
	}
}
